<template>
  <div class="container">
    <div class="my-card">
      <h2>登録情報修正</h2>
      <hr>
      <form
          ref="form"
      >
        <div class="tags">
          <label for="name">名前</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          id="name"
          v-model="form.name"
          type="text"
          placeholder="名前"
        />
        <p class="error" v-if="form.errors.name">{{ form.errors.name }}</p>
        <div class="tags">
          <label for="kananame">名前(カナ)</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          id="kananame"
          v-model="form.kanaName"
          type="text"
          placeholder="名前(カナ)"
        />
        <p class="error" v-if="form.errors.kanaName">{{ form.errors.kanaName }}</p>
        
        <div class="tags">
          <label for="name">生年月日</label>
          <span class="required">必須</span>
        </div>
        <div class="birthday-wrapper">
          <div class="birthday year">
            <MySelect
              v-model="form.birthYear"
              :options="years"
            >
              <template slot-scope="props">
                <option v-for="(option, key) in props.options"
                    :key="key"
                    :value="option.id"
                    :selected="form.birthYear === option.id"
                >{{ option.value }}
                </option>
              </template>
            </MySelect>
          </div>
          年
          <div class="birthday month">
            <MySelect
              v-model="form.birthMonth"
              :options="months"
              :width = 50
            >
              <template slot-scope="props">
                <option v-for="(option, key) in props.options"
                    :key="key"
                    :value="option.id"
                    :selected="form.birthMonth === option.id"
                >{{ option.value }}
                </option>
              </template>
            </MySelect>
          </div>
          月
          <div class="birthday day">
            <MySelect
              v-model="form.birthDay"
              :options="days"
              :width = 50
            >
              <template slot-scope="props">
                <option v-for="(option, key) in props.options"
                    :key="key"
                    :value="option.id"
                    :selected="form.birthDay === option.id"
                >{{ option.value }}
                </option>
              </template>
            </MySelect>
          </div>
          日
        </div>
        <p class="error" v-if="form.errors.birthday">{{ form.errors.birthday }}</p>
            
        <div class="tags">
          <label for="email">メールアドレス</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          id="email"
          v-model="form.email"
          type="text"
          placeholder="メールアドレス"
        />
        <p class="error" v-if="form.errors.email">{{ form.errors.email }}</p>
        
        <div class="tags">
          <label for="thumbnaiil">アイコン画像</label>
          <span class="required">必須</span>
        </div>
        <MyFileInput2
          v-model="form.thumbnail"
        />
        <p class="error" v-if="form.errors.thumbnail">{{ form.errors.thumbnail }}</p>
        
        <div class="tags">  
          <label for="clinicName">医院名</label>
          <span class="required">必須</span>
        </div>

        <MyInput
        id = "clinicName"
        v-model="form.clinicName"
        :type="'text'"
        placeholder="医院名"
        />
        <p class="error" v-if="form.errors.clinicName">{{ form.errors.clinicName }}</p>
        
        <div class="tags">
          <label for="clinicAddress">医院住所</label>
          <span class="required">必須</span>
        </div>

        <MyInput
        id = "clinicAddress"
        v-model="form.clinicAddress"
        :type="'text'"
        placeholder="医院住所"
        />
        <p class="error" v-if="form.errors.clinicAddress">{{ form.errors.clinicAddress }}</p>
        
                
        <div class="tags">
          <label for="phonenumber">連絡先電話番号</label>
          <span class="required">必須</span>
        </div>
        <MyInput
          id="phonenumber"
          v-model="form.phonenumber"
          type="text"
          placeholder="電話番号"
        />
        <p class="error" v-if="form.errors.phonenumber">{{ form.errors.phonenumber }}</p>
        
        <div class="tags">
          <label for="notification">受領通知</label>
          <span class="required">必須</span>
        </div>
        <div class="notification-wrapper">
          <MySelect
            id="notification"
            v-model="form.notification"
            :options = "notifications"
          >
            <template slot-scope="props">
              <option v-for="(option, key) in props.options"
                  :key="key"
                  :value="option.id"
                  :selected="form.birthDay === option.id"
              >{{ option.value }}
              </option>
            </template>
          </MySelect>
        </div>
        <p class="error" v-if="form.errors.notification">{{ form.errors.notification }}</p>
        
        <div class="spacer"></div>
        <MyButton
          caption="更新"
          bgcolor="#db362e"
          @click="post"
        />
        <div class="change-password">
        パスワードの変更は<a href="#">こちら</a>
        </div>
      </form>
    </div>
  </div>
</template>
<style scoped>
  .container {
    text-align: center;
  }
  .my-card {
    display: inline-block;
    width: 400px;
    border-radius: 10px;
    margin: 2rem 0;
    padding: 2rem;
    background-color: white;
    border: 1px solid rgba(0,0,0,.125)
  }
  .tags {
    text-align: left;
    margin-top: 20px;
  }
  .required {
    display: inline-block;
    padding: 2px;
    color: white;
    font-weight: bold;
    font-size: 12px;
    background-color: red;
    border-radius: 3px;
  }
  .birthday-wrapper {
    display: flex;
    margin-top: 10px;
    line-height: 40px;
  }
  .birthday.year {
    width: 30%;
  }
  .birthday.month {
    width: 20%;
  }
  .birthday.day {
    width: 20%;
  } 
  .notification-wrapper {
    margin: 10px 0;
  }
  .spacer {
    height: 10px;
  }
  p.error {
    background-color: red;
    color: white;
    border-radius: 3px;
    text-align: left;
    padding: 0 5px;
  }
</style>
<script>
import MyInput from './components/MyInput'
import MyButton from './components/MyButton'
import MySelect from './components/MySelect'
import MyFileInput2 from './components/MyFileInput2'
export default {
  components: {
    MyInput,
    MyButton,
    MySelect,
    MyFileInput2
  },
  data () {
    return {
      form: {
        name: this.$store.getters['auth/userInfo'].name,
        kanaName: this.$store.getters['auth/userInfo'].furigana,
        birthYear: 1920,
        birthMonth: 1,
        birthDay: 1,
        thumbnail: null,
        email: '',
        phonenumber: '',
        notification: 0,
        errors: {
          name: 'hgoe'
        }
      }
    }
  },
  computed: {
    userInfo: function () {
      alert('hoge')
      console.log(this.$store.getters['auth/userInfo'])
      return this.$store.getters['auth/userInfo'] 
    },
    years: function () {
      let arr = []
      let startYear = 1920
      let lastYear = 2020
      for (let i = startYear; i <= lastYear; i++) {
        arr.push({ id: i, value: i })
      }
      return arr
    },
    months: function () {
      let arr = []
      let startMonth = 1
      let lastMonth = 12
      for (let i = startMonth; i <= lastMonth; i++) {
        arr.push({ id: i, value: i })
      }
      return arr
    },
    days: function () {
      let arr = []
      let startDay = 1
      let lastDay = 12
      for (let i = startDay; i <= startDay; i++) {
        arr.push({ id: i, value: i })
      }
      return arr
    },
    notifications: function () {
      return [
        { id: 0, value: '毎回通知' },
        { id: 1, value: '30毎' },
        { id: 2, value: '1時間' },
        { id: 3, value: '通知しない' }    
      ]
    },
    
  },
  methods: {
    validate: function () {
      let isValid = true
      if (!this.form.name || this.form.name === '') {
        this.form.errors.name = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.kanaName || this.form.kanaName === '') {
        this.form.errors.kanaName = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.thumbnail) {
        this.form.errors.thumbnail = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.email || this.form.email === '') {
        this.form.errors.email = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.phonenumber || this.form.phonenumber === '') {
        this.form.errors.phonenumber = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.clinicName || this.form.clinicName === '') {
        this.form.errors.clinicName = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.clinicAddress || this.form.clinicAddress === '') {
        this.form.errors.clinicAddress = 'この項目は必須です'
        isValid = false
      }
      if (!this.form.notification || this.form.notification === '') {
        this.form.errors.notification = 'この項目は必須です'
        isValid = false
      }
      return isValid
    },
    async post () {
      if (!this.validate()) {
        return false
      }
      try {
        await this.$store.dispatch(
          'auth/create',
          this.form
        )
        // login successfull
        this.$router.push('/chat')
      } catch (err) {
        const { status, data } = err.response
        let message
        if (status === 400) {
          message = 'ログインに失敗しました'
          console.log(data)
        } else {
          message = '予期しないエラーが起きました' + data
        }
        this.flash(message, 'error', {
          timeout: 3000
        })
      }
    }
  },
  mounted () {
    this.$store.dispatch(
      'auth/destroy'
    )
  }
}
</script>



