<template>
  <div
    :class="{'conversation-row': true, active: room.id === activeRoomId }"
    v-on:click="onRoomClick(room.id)"
  >
    <div class="room-title">{{ room.title }}</div>
    <div
      class="room-members"
      v-for="(member, id) in room.members"
      :key = id
    >
      <img class="user-thumbnail" :src="hostName + userInfo.thumbnail">
      <span
        class="room-member"
      >
        {{ member.user.name }}
      </span>
    </div>
   </div>
</template>
<style scoped>
.wrapper {
  display: relative
}
.conversation-row {
  font-size: 20px;
  position :relative;
  text-align: center;
  background-color: transparent;
  padding: 5px;
	color: white;
	border: 1px solid white
}

p {
  text-align: left;
}
.user-thumbnail {
  max-width : 30px;
  max-height : 30px;
  display: inline-block
}
.room-members {
  display: flex;
  height:30px;
  border: solid gray 1px;
  line-height:30px;
}
.room-title {
  font-size: 20px;
}
.room-member {
  font-size: 14px;
  display:inline-block;
  line-height: 30px;
}
.active {
	border-right: 2px solid	red;
}
.conversation-row:hover {
  cursor: pointer
}
</style>
<script>
import moment from 'moment'
export default {
  props: {
    room: { type: Object, requred: true }
  },
  mounted () {
    console.log(this.room)
  },
  computed: {
    activeRoomId: function () {
      return this.$store.getters['chat/activeRoomId']
    },
    hostName: function () {
      return process.env.HOSTNAME
    },
    userInfo: function () {
      return this.$store.getters['auth/userInfo']
    }
	},
  filters: {
    format: function (value) {
      return moment(value * 1000).format('YYYY/MM/DD HH:mm')
    }
  },
  methods: {
    onRoomClick: function (id) {
      if (id !== this.activeRoomId) {
			  this.$store.dispatch('chat/enter',{ roomId: id })
      }
    }
  }
}
</script>



