import { get } from "https"

export default {
  namespaced: true,
  state: {
  },
  mutations: {
	setRooms (state, data) {
      state.rooms = data.rooms
    },
    join (state, data) {
      state.rooms = data.rooms
    },
    enter (state, data) {
      state.roomId = data.roomId
    },
    destroy (state) {
      state.roomId = ''
		}
  },
  getters: {
		// authToken: (state, getters, rootState, rootGetters) => {
		// 	console.log(rootGetters['auth/token'])
		// 	return rootGetters['auth/token']
		// },
		socket: (state, getters, rootState, rootGetters) => {
			return rootGetters.socket
		},
		loading: (state, getters, rootState, rootGetters) => {
			return rootState.rooms.loading
		},
		rooms: (state, getters, rootState, rootGetters) => {
			return rootState.rooms.data
		},
		messages: (state, getters, rootState, rootGetters) => {
			let result = getters.rooms.filter(function(item, id) {
				if (item.id === getters.activeRoomId) {
					return true
				}
			})
			if (result.length > 0) {
				return result[0].messages
			} else {
				return []
			}
		},
		files: (state, getters, rootState, rootGetters) => {
			return rootState.files.data
		},
		activeRoomId: (state, getters, rootState, rootGetters) => {
			return rootState.rooms.activeId
		}
  },
  actions: {
	connect ({ commit, dispatch }, data) {
		dispatch('connect', null, { root: true })
	},
	getRooms ({ commit, dispatch }, data) {
		dispatch('getRooms', null, { root: true })
	},
	getRoomFiles ({ commit, dispatch }, data) {
		dispatch('getRoomFiles', null, { root: true })
	},
	subscribe ({ commit, dispatch }, data) {
		dispatch('subscribe', data, { root: true })
	},
	enter ({ commit, dispatch }, data) {
		dispatch('enter', data, { root: true })
	},
	leave ({ commit, dispatch }, data) {
		dispatch('leave', data, { root: true })
	},
	sendMessage ({ commit, dispatch }, data) {
		dispatch('sendMessage', data, { root: true })
	}
  }
}



