<template>
  <input
    :type="type"
    :placeholder="placeholder"
    v-bind:value="value"
    v-on:input="$emit('input', $event.target.value)"
  />
</template>
<style scoped>
  input {
    width: 100%;
    height: 40px;
    border: solid lightgray 1px;
    border-radius:5px;
    padding: 3px 10px;
    margin: 5px 0;
  }
</style>
<script>
export default {
  name: 'MyInput',
  props: {
    value: { type: String },
    type: { type: String, requre: true },
    placeholder: { type: String }
  }
}
</script>



