<template>
  <div class="input-area">
    <MainSectionInputAreaFileThumbnail
      v-for="(file, id) in data.files"
      :key="id"
      :file="file"
      :count="id"
      v-on:delete="deleteFile(id)"
    />
    <div class="text-div">
      <MyTextArea
        v-model="data.messagetext"
      />
      <button
        class="send-btn"
        v-on:click="sendMessage"
      >
        送信
      </button>
      <MyFileInput
        v-model="data.files"
      />
    </div>
		<Modal
        v-show="modal.show"
        :isError="false"
      >
        <template
          v-slot:body
        >
          <p>
						{{ modal.message }}
          </p>
				<button
					class="modal-close-button"
					v-on:click="modal.show = false"
				>
					閉じる
				</button>
        </template>
      </Modal>
  </div>
</template>
<style scoped>
    /* 入力部分 */
    .input-area {
      width: 100%;
      height: 15vh;
      display: flex;
      background-color: white;
      padding: 3px
    }
    .text-div {
      width: 100%;
      margin-bottom: 10px;
      position: relative;;
    }
    .send-btn {
      color: white;
      position:absolute;
      top: 10px;
      font-size: 12px;
      padding: 3px 10px;
      border-radius: 3px;
      background-color: teal;
      right: 10px;
    }
    .attach-file-div {
      width: 10%
		}
		.modal-close-button {
			background-color: red;
			color: white;
			padding: 4px;
			border-radius: 5px;	
		}
</style>
<script>
import Modal from './Modal'
import MyTextArea from './MyTextArea'
import MyFileInput from './MyFileInput'
import MainSectionInputAreaFileThumbnail from './MainSectionInputAreaFileThumbnail'
export default {
  data () {
    return {
			modal: {
				show: false,
				message: '',
			},
      data: {
				messagetext: '',
				files: []
      }
    }
  },
  computed: {
    filecount: function () {
      return this.data.files.length
    }
  },
  watch: {
    'data.files': function (data) {
      console.log(data)
    }
  },
  methods: {
    getJsonData: async function (id_list) {
      return {
        'command': 'send',
        'room': this.$store.state.chat.roomId,
        'message': this.data.messagetext,
        'attachment': id_list
      }
    },
    deleteFile: function (id) {
      this.data.files.splice(id, 1)
    },
    sendMessage: async function () {
			if (this.data.messagetext === '') {
				this.modal.message = 'メッセージを入力してください'
				this.modal.show = true
				return false
			}
			let uploaded_files = await this.uploadFile(this.data.files)
			let id_list = []
			for (let i = 0; i < uploaded_files.length; i++) {
				id_list.push(uploaded_files[i].id)
			}
			this.$store.dispatch('sendMessage', { message: this.data.messagetext, attachment: id_list })
			this.data.messagetext = ''
			this.data.files = []
		},
		uploadFile: async function (files) {
			try {
				let formData = new FormData()
				for (let i = 0; i < files.length; i++) {
					formData.append(files[i].name, files[i])
				}
        let data = await this.$store.dispatch(
        	'http/post',
					{ url: 'chat/upload/',data: formData, content_type: 'multipart/form-data' }
				)
				return data.data
		 	} catch (err) {
        const { status, data } = err.response
        let message
        if (status === 400) {
          message = 'ログインに失敗しました'
          console.log(data)
        } else {
          message = '予期しないエラーが起きました' + data
        }
        this.flash(message, 'error', {
          timeout: 3000
				})
			}
		},
    toBase64: async function (file) {
      return new Promise((resolve, reject) => {
        const reader = new FileReader()
        reader.readAsDataURL(file)
        reader.onload = () => resolve(reader.result)
        reader.onerror = error => reject(error)
      })
    }
  },
  components: {
    MyFileInput,
    MyTextArea,
		MainSectionInputAreaFileThumbnail,
		Modal
  }
}
</script>



