<template>
  <div>
    <div class="gmenu">
      <div class="gmenuArea">
          <ul class="gmenu">
            <li class="gmenu">
                <a href="" class="gmenu">
                    <i class="material-icons" id="menuIcon">home</i>
                </a>
            </li>
            <li class="gmenu">
                <!-- ドロワーメニュー -->
                <div class="drawer">
                <ul>
                  <li
                    v-for="(r, index) in rooms"
                    :key="index"
                  >
                    <a href="#/chat">{{ r.title }}</a>
                  </li>
                </ul>
                <div class="drbtn">
                    <span  class="open"><i class="material-icons" id="menuIcon">chat</i></span>
                    <span class="clear"><i class="material-icons" id="menuIcon">clear</i></span>
                </div>
                </div>
            </li>
          </ul>
      </div>
    </div>

    <div class="flex">
      <!-- ユーザ情報 -->
      <div class="smallArea">
        <div class="userIcon">
          <img class="user-thumbnail" :src="hostName + userInfo.thumbnail">
        </div>           
        <div class="userName"><label class="name">{{ userInfo.name }}</label></div>
          <div class="calendar">
            <h2 class="calendar-title"><span id="js-year"></span>年 <span id="js-month"></span>月</h2>
            <table class="calendar-table">
              <thead>
                <tr><th>日</th><th>月</th><th>火</th><th>水</th><th>木</th><th>金</th><th>土</th></tr>
              </thead>
              <tbody id="js-calendar-body"></tbody>
            </table>
          </div>
        </div>

          <!-- 履歴 -->
        <div class="largeArea">
            <ul class="hist">
                <li
                  v-for="(r, index) in rooms"
                  :key="index"
                >
                  {{ r.title }}
                </li>
            </ul>
        </div>
      </div>  
    </div>
</template>
<style scoped>
  @import '../assets/css/dashboard.css';
  .user-thumbnail {
    width: 100px;
    height: auto;
  }
</style>
<script>
import $ from 'jquery'
export default {
  data () {
    return {
      rooms: []
    }
  },
  computed: {
    socket: function () {
      return this.$store.getters['chat/socket']
    },
    userInfo: function () {
      return this.$store.getters['auth/userInfo']
    },
    loading: function () {
      return this.$store.getters['chat/loading']
    },
    activeRoomId: function () {
      return  this.$store.getters['chat/activeRoomId']
    },
    hostName: function () {
      return process.env.HOSTNAME
    }
  },
  methods: {
    getRooms: async function () {
      let url = 'chat/chatrooms/'
      try {
        let result = await this.$store.dispatch('http/get',{ url: url })
        this.rooms = result.data
    } catch (err) {
        alert(' getRooms 182 line error')
        alert('err' + err)
      }
    }
  },
  mounted () {
    this.getRooms()
    $(function(){
      var $window = $(window);
      var $year = $('#js-year');
      var $month = $('#js-month');
      var $tbody = $('#js-calendar-body');

      var today = new Date();
      var currentYear = today.getFullYear(),
      currentMonth = today.getMonth();
      calendarHeading(currentYear, currentMonth);
      calendarBody(currentYear, currentMonth, today);


      function calendarBody(year, month, today){
        var todayYMFlag = today.getFullYear() === year && today.getMonth() === month ? true : false; // 本日の年と月が表示されるカレンダーと同じか判定
        var startDate = new Date(year, month, 1); // その月の最初の日の情報
        var endDate  = new Date(year, month + 1 , 0); // その月の最後の日の情報
        var startDay = startDate.getDay();// その月の最初の日の曜日を取得
        var endDay = endDate.getDate();// その月の最後の日の曜日を取得
        var textSkip = true; // 日にちを埋める用のフラグ
        var textDate = 1; // 日付(これがカウントアップされます)
        var tableBody =''; // テーブルのHTMLを格納する変数
        
        for (var row = 0; row < 6; row++){
          var tr = '<tr>';
          
          for (var col = 0; col < 7; col++) {
            if (row === 0 && startDay === col){
              textSkip = false;
            }
            if (textDate > endDay) {
              textSkip = true;
            }
            var addClass = todayYMFlag && textDate === today.getDate() ? 'is-today' : '';
            var textTd = textSkip ? ' ' : textDate++;
            var td = '<td class="'+addClass+'">'+textTd+'</td>';
            tr += td;
          }
          tr += '</tr>';
          tableBody += tr;
        }
        $tbody.html(tableBody);
      }

      function calendarHeading(year, month){
        $year.text(year);
        $month.text(month + 1);
      }
      $('.drbtn').on('click', function () {
        $(this).toggleClass('action');
        $('.drawer').toggleClass('action');
      });
    });

      
  }
}
</script>


