<template>
  <div class="wrapper">
    <div
      class="title"
    >
      Feedback
    </div>
    <button v-on:click="getCommentCount(activeRoomId)">action2</button>
  </div>
</template>
<style scoped>
  /* main panel */
  div.wrapper {
    width: 30vw;
    height: 90vh;
    background-color: #f0eafe;
    overflow-x: scroll;
    overflow-y: scroll;
  }
  .title {
    width: 100%;

    background-color: transparent;
    color: gray
  }

  span {
    display: inline-block;
  }

  .filename {
    display: inline-block;
    line-height: 45px;
    padding: 0 20px;
    text-align:left
  }
</style>
<script>
import moment from 'moment'
export default {
  components: {
  },
  props: {
  },
  methods: {
    getFileExtension: function (filename) {
      return filename.slice((filename.lastIndexOf(".") - 1 >>> 0) + 2);
    },
    isImage: function (val) {
      var globalRegex = RegExp('jpe?g|png','g');
      return globalRegex.test(val)
    },
    getCommentCount: async function (roomId) {
      try {
        const postdata = {
          action: 2,
          roomid: roomId,
          userid: this.$store.getters['auth/userId']
        }
        const endpoint = 'https://saychat-backend.azurewebsites.net/api/Function1'
        let data = await this.$store.dispatch(
        	'http/post',
					{ fullUrl: endpoint, data: postdata }
        )
        console.log(data)
      } catch (err) {
        alert('error')
      }
    },
  },
  computed: {
    loading: function () {
      return this.$store.getters['chat/loading']
    },
    files: function () {
      return this.$store.getters['chat/files']
    },
    activeRoomId: function () {
      return this.$store.getters['chat/activeRoomId']
    },
    hostName: function () {
      return process.env.HOSTNAME
    },
    userInfo: function () {
      return this.$store.getters['auth/userInfo']
    }
  },
  filters: {
    toYYYYMMDD: function (data) {
      return moment(data).format('YYYY/MM/DD')
    },
    shorten: function (val) {
      let maxLength = 12
      if (val.length > maxLength) {
        return val.slice(0, maxLength) + "..."
      } else {
        return val
      }
    }
  }
}
</script>



