<template>
  <div>
    <Navbar />
    <div class="content">
      <RoomsSection/>
      <MainSection />
      <Feedback />
      <Modal
        v-show="socket.isConnecting || socket.isReconnecting"
        :isError="false"
      >
        <template
          v-slot:body
        >
          <p
            v-if="socket.isReconnecting"
          >
            再接続中....
          </p>
          <p
            v-if="socket.isConnecting"
          >
            接続中....
          </p>
        </template>
      </Modal>
    </div>
  </div>
</template>
<style scoped>
  div.content {
    height: 90vh;
    width: 100vw;
    display: flex;
    flex-direction: row;
  }
</style>
<script>
import Navbar from './Navbar'
import RoomsSection from './components/RoomsSection'
import MainSection from './components/MainSection'
import Modal from './components/Modal'
import Feedback from './components/Feedback'

import Vue from 'vue'

export default {
  components: {
    RoomsSection,
    MainSection,
    Navbar,
    Modal,
    Feedback
  },
  computed: {
    socket: function () {
      return this.$store.getters['chat/socket']
    },
    activeRoomId: function () {
      if (this.$store.state.chat.roomId === '') {
        return null
      } else {
        return this.$store.state.chat.roomId
      }
    },
    hostName: function () {
      return process.env.HOSTNAME
    }
  },
  watch: {
    'activeRoomId': async function (data) {
      this.fetchMessages()
      this.fetchFiles()
    },
  },
  methods: {

  },
  async mounted () {
    await this.$store.dispatch('connect')
  }
}
</script>



