<template>
  <div class="base">
    <div class="wrapper">
      <div class="login-card">
        <form
          ref="form"
        >
          <MyInput
            v-model="form.username"
            type="text"
            placeholder="メールアドレス"
          />
          <MyInput
            v-model="form.password"
            :type="'password'"
            placeholder="パスワード"
          />

          <MyButton
            caption="ログイン"
            bgcolor="#db362e"
            @click="login"
          />
        </form>
      </div>
    </div>
  </div>
</template>
<style scoped>
  .base {
    height: 90vh;
    width:  100vw;
    text-align: center;
    background-color: #f8f9fa
  }
  .wrapper {
    width: 500px;
    display: inline-block;
  }
  .logo {
    margin-top: 1rem;
    width: 80%;
    height: auto
  }
  .login-card {
    width: 100%;
    border-radius: 10px;
    margin: 2rem 0;
    padding: 2rem;
    background-color: white;
    border: 1px solid rgba(0,0,0,.125)
  }
  .forget-password {
    text-align: left
  }
  p {
    margin: 10px 0;
  }
</style>
<script>
import MyInput from './components/MyInput'
import MyButton from './components/MyButton'

export default {
  components: {
    MyInput,
    MyButton
  },
  data () {
    return {
      form: {
        username: '',
        password: ''
      }
    }
  },
  methods: {
    async login () {
      try {
        await this.$store.dispatch(
          'auth/create',
          this.form
        )
        // login successfull
        this.$router.push('/dashboard')
      } catch (err) {
        const { status, data } = err.response
        let message
        if (status === 400) {
          message = 'ログインに失敗しました'
          console.log(data)
        } else {
          message = '予期しないエラーが起きました' + data
        }
        this.flash(message, 'error', {
          timeout: 3000
        })
      }
    }
  },
  mounted () {
    this.$store.dispatch(
      'auth/destroy'
    )
  }
}
</script>



