<template>
   <v-textarea
      class="message-textarea"
      autofocus
      placeholder="入力してください"
      v-on:input="update"
			:value="value"
	  ></v-textarea>
</template>
<style scoped>
.message-textarea {
  text-align: left;
  height: 100%;
  width: 100%;
  border-radius: 10px;
  resize: none;
  overflow: scroll;
  padding: 30px 100px 5px 10px;
  background-color: white;
  border: lightgray solid 1px;

}
</style>
<script>
export default {
  props: {
    value: { type: String, required: true }
  },
  methods: {
    update: function (e) {
      this.$emit('input', e)
    }
  }
}
</script>



