<template>
  <button
    v-on:click="$emit('click', $event.target)"
    v-bind:style="{ 'background-color': bgcolor }"
  >
    {{ caption }}
  </button>
</template>
<style scoped>
  button {
    color: white;
    width : 100%;
    border-radius: 5px;
    display: block;
    height: 40px;
    margin : 10px 0;
  }
</style>

<script>
export default {
  name: 'MyButton',
  props: {
    caption: { type: String },
    bgcolor: { type: String }
  }
}
</script>



