<template>
  <div
    class="file-wrapper"
  >
    <img
      src="@/assets/images/file.png"
      class="file-image"
    >
    <span
      class="filename"
    >
      <a :href="fileInfo.file_url">
      {{ fileInfo.file_name | shorten }}
      </a>
    </span>
  </div>
</template>
<style scoped>
  .file-wrapper {
    width: 100px;
    height: auto  ;
    display:inline-block;
    margin: 3px;
  }
  .file-image {
    height: auto;
    width: 100%;
    border-radius: 5px;
    margin:none
  }
  .filename {
    display: block;
    line-height: 12px;
  }
  a {
    color:gray;
    font-weight: bold;
    font-size: 12px;
  }
</style>
<script>
export default {
	props: {
		fileInfo: { type: Object }
	},
  data () {
    return {
      loading: false,
    }
  },
  computed: {
  },
  filters: {
    shorten: function (val) {
      let maxLength = 20
      if (val.length > maxLength) {
        return val.slice(0, maxLength) + "..."
      } else {
        return val
      }
    }
  },
  methods: {
  }
}
</script>



