<template>
  <div class="side-panel">
    <div
      class="loader"
      v-show="loading"
    >loading</div>
    <RoomsSectionRow
      v-for="(r, id) in rooms"
      :key="id"
      :room=r
    />
  </div>
</template>
<style scoped>
    div.side-panel {
      width: 20vw;
      height:90vh;
      background-color:teal;
      box-shadow: 1px 1px 1px rgba(0,0,0,.125);
      z-index: 2
    }
</style>
<script>
import RoomsSectionRow from './RoomsSectionRow'
export default {
  components: {
    RoomsSectionRow
  },
  props: {
  },
  computed: {
    socket: function () {
      return this.$store.getters['chat/socket']
    },
    loading: function () {
      return this.$store.getters['chat/loading']
    },
    rooms: function () {
      return this.$store.getters['chat/rooms']
    },
    activeRoomId: function () {
      return  this.$store.getters['chat/activeRoomId']
    }
  },
  watch: {
    'socket.isConnected': function (data) {
      if (data && !this.socket.isReconnected) {
        this.$store.dispatch('chat/getRooms', {})
      }
    },
   	activeRoomId: async function (newVal, oldVal) {
      this.$store.dispatch('chat/getRoomFiles')
      if (oldVal) {
        this.$store.dispatch('chat/leave', {roomId: oldVal}) 
      }
    }
  },
  async mounted () {
    // await this.fetchRooms()
  },
  methods: {
  }
}
</script>



