<template>
  <div
    class="message-area"
    ref="container"
  >
    <div v-show="loading" class="loader">loader</div>
    <div
      class="wrapper"
      v-if="messages"
      v-for="(message, id) in messages"
      :key = id
    >
      <div
        :class="{ 'message-row': true, me: message.speaker.id === myUserId, notme: message.speaker.id !== myUserId }"
      >
        <div class="message-body">
          <span class="speaker-name">{{ message.speaker.name }}</span>
          <p class="message-text">{{ message.message }}</p>
          <span
            class="thumbup"
            v-for="(thumbup, id) in message.mythumbups"
            :key="id"
            :src="hostName + thumbup.sender.thumbnail"
          >
            <img
              class="thumbup-user-thumbnail"
              :src="hostName + thumbup.sender.thumbnail"
            />
          </span>
        
        </div>
         <span class="sent-at">
        {{ message.sent_at | formatDateTime }}
        </span>
        <button class="thumbup-btn" v-on:click="onThumbupBtnClick(message)" v-show="message.speaker.id !== myUserId">
          いいね！！
        </button>
      </div>
    </div>
  </div>
</template>
<style scoped>
  div.main-panel .message-area {
    width: 100%;
    height: 70vh;
    background-color: white;
    z-index: 2;
    overflow-y:  scroll;
    padding:10px 1rem;
  }
  .wrapper {
    width: 100%;
    border-bottom: solid lightgray 1px;
  }
  .attachment-row {
    display:flex;
    margin: 5px;
  }
  .thumbup-user-thumbnail {
    height: 25px;
    width: 25px;
    border-radius: 50%;
  }
  .thumbup-btn {
    position: absolute;
    bottom: 0px;
    right: 0px;;
    color:white;
    border-radius: 3px;
    background-color: teal;
    font-size: 10px;
    padding: 3px;
    height: 30px;
    margin: 3px;
    display: inline-block;
  }
  /* message row */
  .message-row {
    display: flex;
    position: relative;
    padding: 2px;
    margin: 5px 0 0 0;
  }
  .message-row.notme {
    flex-direction : row
  }
  
  .user-thumbnail {
    width: 5%;
    text-align: center;
    margin: 0 1rem
  }
  .user-thumbnail > img {
    width : 30px;
    height: 30px;
    border-radius: 50%;
  }
  .message-body {
    /* width: 70%; */
    min-height:  50px;
    text-align: left;
  }
  .speaker-name {
    color: blue;
    display: block;
  }
  .message-text {
    font-size: 12px;
  }
  .sent-at {
    color: gray;
    position: absolute;
    top: 5px;
    right: 10px;
    font-size: 12px;
    display: inline-block
  }

  .tags {
    display: flex;
    font-size: 12px;
    margin: 0 1rem
  }
  .kidoku {
    display: inline-block;
    margin-right: 1rem;
    font-weight: bold
  }
  .read {
    color: gray
  }
  .notread {
    color: red
  }
  
</style>
<script>
import MainSectionMessageAreaFileThumbnail from './MainSectionMessageAreaFileThumbnail'
import moment from 'moment'
export default {
  props: {
  },
  components: {
    MainSectionMessageAreaFileThumbnail
  },
  computed: {
    loading: function () {
      return this.$store.getters['chat/loading']
    },
    rooms: function () {
      return this.$store.getters['chat/rooms']
    },
    messages: function () {
      return this.$store.getters['chat/messages']
    },
    activeRoomId: function () {
      return this.$store.getters['chat/activeRoomId']
    },
    activeRoom: function () {
      console.log(this.rooms)
      return this.rooms.find(item => item.id === this.activeRoomId)
    },
    myUserId: function () {
      return this.$store.getters['auth/userId']
    },
    hostName: function () {
      return process.env.HOSTNAME
    }
  },
  watch: {
    'activeRoomId': function (val) {
    }
  },
  filters: {
    toKidoku: function (bool) {
      if (bool) {
        return '既読'
      } else {
        return '未読'
      }
    },
    formatDateTime: function (val) {
      return moment(val * 1000).format('MM/DD h:mm')
    }
  },
  methods: {
    toBottom: function () {
      var container = this.$refs.container
      container.scrollTop = container.scrollHeight
    },
    onThumbupBtnClick: async function (message) {
      console.log(message)
      try {
        let postData = {
          'action': 'add',
          'target': message.pk
        }
        let data = await this.$store.dispatch(
        	'http/post',
					{ url: 'chat/thumbup/',data: postData }
				)
				return data.data
	    } catch (err) {
        alert('in message area catch error' + err)
      }
    }
  },
  updated () {
    this.toBottom()
  }
}
</script>



