<script>
import MainSectionRoomTitle from './MainSectionRoomTitle'
import MainSectionMessageArea from './MainSectionMessageArea'
import MainSectionInputArea from './MainSectionInputArea'
export default {
  components: {
    MainSectionRoomTitle,
    MainSectionMessageArea,
    MainSectionInputArea
  },
  props: {
  },
  computed: {
  }
}
</script>



